import { useState } from 'react';
import './SingleQuestion.css';
import Answer from './answer';

function SingleQuestion ({title, content}) {
    const [show, setShow] = useState("");
    const [turn, setTurn] = useState("back");

    function fullSize () {
        if (show != "") {
            setShow("");
            setTurn("back");
        }
        else {
            setShow(content);
            setTurn("back2");
        }
    }

    return (
        <div>
            <div className="container"
            onClick={fullSize}
            >
                <div className='before'>?</div>
                <p>{title}</p>
                <div className={turn}>^</div>
            </div>
            <div className='textboxFAQ'> 
                <Answer content={show} />
            </div>
        </div>
    );
}

export default SingleQuestion;