import { motion } from "framer-motion";
import { useEffect, useState } from "react";

function Answer ({content}) {
    const [x, setX] = useState(0);

    useEffect (() => {
        if (x == 0 && content !== "") setX(100);
        else setX(0);
    }, [content]);

    if (content !== ""){
        return (
            <motion.div
            initial={{ height: 0 }}
            animate={{height: x}}
            >
            <div>
                <p>
                <div className="mark">!</div>
                <div className="content">{content}</div>
                </p>
            </div>
            </motion.div>
        );
    }else{
        return (
            <motion.div
            initial={{ height: 0 }}
            animate={{height: x}}
            >
            <div>
            </div>
            </motion.div>
        );
    }
}

export default Answer;